@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Poppins';
}
.App
{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  position: -webkit-sticky;
  position: sticky; 
  top: 0;
  padding:60px 30px 0px 30px ;
  background: rgb(238,103,143);
  background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
}
.condiv
{
  padding: 100px ;
  background-color: #f4f5f9;
  -webkit-flex:6;
          flex:6;
  -webkit-flex:var(--condiv-flex);
          flex:var(--condiv-flex);
}

.home
{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  
}
.profilepic
{
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}
.typingeffect
{
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
}
.resumeLink
{
  text-decoration: none;
  color: black;
  font-size: 14px; 
}

.socialLink
{
  padding: 15px
}

nav ul{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li
{
  display: inline;
  margin:40px;
}

a
{
  text-decoration: none;
  color: black;
}

.subtopic
{
  margin:10px 10px 30px 10px !important;
}
p{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: -webkit-flex;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}
.skillsContainer {
  width: 100%; /* Full width */
  background-color: #ddd; /* Grey background */
}

.skills {
  padding-top: 10px; /* Add top padding */
  padding-bottom: 10px; /* Add bottom padding */
}

.java {width: 90%; background-color: #4CAF50;} /* Green */
.html {width: 80%; background-color: #2196F3;} /* Blue */
.css {width: 65%; background-color: #f44336;} /* Red */
.javascript {width: 60%; background-color: #808080;} /* Dark Grey */

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --typing-font:2rem;
  }
  .App
  {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  .condiv
  {
    padding: 50px ;
  }
  nav ul
  {
    
    display: -webkit-flex;
    
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }

  

}

.bar_content {
  display: block; /* Important to give the content a width */

  width: 100%;
  height: 2%;
  -webkit-transform: translateX(100%);
          transform: translateX(100%); /* Animation start out of the screen */

  /* Add the animation */
  -webkit-animation: move 30s linear infinite /* infinite make reapeat the animation indefinitely */;
          animation: move 30s linear infinite /* infinite make reapeat the animation indefinitely */;
}

.bar {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 2%;
  padding: 15px 0;

  background-color: rgba(117, 112, 112, 0.288);
  color: white;

  font-family: 'Raleway';
  font-size: 14px; 
}

/* Create the animation */
@-webkit-keyframes move {
  to { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}
@keyframes move {
  to { -webkit-transform: translateX(-100%); transform: translateX(-100%); }
}
